.about {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
}

.container {
    width: 380px;
    height: 500px;
    position: relative;
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 16px;
    overflow: hidden;
}

.container img {
    width: 100%;
    height: 100%;
}

.inner_container {
    position: absolute;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    bottom: 0px;
    padding: 15px 0;
    padding-bottom: 25px;
    background: #fff;
    height: 100px;
    transition: all 0.3s ease;
}

.inner_container h2,
.inner_container p {
    color: #49bf4cff;
    font-style: bold;
    font-size: 25px;
}

.icons_container {
    position: absolute;
    bottom: 100px;
    display: flex;
    justify-content: center;
    background: #fff;
    width: 100%;
    padding: 10px 0 0;
    transform: scaleY(0);
    transform-origin: bottom;
    transition: all 0.3s ease;
}

.icons_container>.icons {
    transform: translateY(50%);
    padding: 10px 20px;
    border-radius: 30px;
    background: #fff;
    display: flex;
    justify-content: center;
    gap: 14px;
    transition: all 0.3s ease;
}

.icons_container>.icons a .i {
    font-size: 20px;
    color: grey;
}

.icons_container > .icons a .i {
    color: grey;
}

.container:hover .icons_container,
.container:hover .inner_container {
    transform: scaleY(1);
    background: #49bf4cff;
    color: #fff !important;
}

.container:hover .inner_container h2,
.container:hover .inner_container p {
    color: #fff !important;
}

.container:hover .icons {
    transform: translateY(-70%);
    background: #fff;
    color: grey;
}

.container:hover .icons>a {
    color: grey;
}

.icons a:hover .i {
    color: #49bf4cff;
}

.containn {
    margin: 1rem;
}

.center img {
    max-height: 250px;
    aspect-ratio: 2/1;
    object-fit: contain;
}

.span.tag {
    position: absolute;
    background: red;
    box-shadow: dark-red 5px 5px;
    padding: 5px;
    top: 3px;
    left: 2px;
    border-radius: 5px;
    color: white;
    font-size: 14px;
}