.work {
    background: #39b54a;
    padding: 2rem;
}

.project {
    background-color: rgba(0, 0, 0, 0.4);
    padding: 2rem;
    border-radius: 20px;
    margin-bottom: 2rem;
    transition: background-color 0.7s ease-in-out;
}

.project:hover {
    background-color: rgba(0, 255, 21, 0.4);
}

.project h3 {
    align-items: center;
    justify-content: space-between;
    width: auto;
    color: white;
}

.project p {
    color: white;
    margin-bottom: 1rem;
}

.project-images {
    display: block;
    width: 100%;
    min-height: 200px;
    z-index: 999;
}

.image-wrapper {
    display: flex;
    height: 100%;
    width: 100%;
    overflow-x: auto;
    /* Enable horizontal scrolling */
    scroll-snap-type: x mandatory;
    /* Enable horizontal scroll snapping */
    -webkit-overflow-scrolling: touch;
    /* Enable smooth scrolling on iOS */

    /* Hide scrollbar */
    scrollbar-width: none;
    /* Firefox */
    -ms-overflow-style: none;
    /* IE and Edge */
    z-index: 999;
    scroll-snap-align: center;
}

.image-wrapper::-webkit-scrollbar {
    display: none;
    /* Chrome, Safari, Opera */
}

/* Center the images within the container */
.image-wrapper>img {
    flex: 0 0 auto;
    /* Prevent images from stretching */
    scroll-snap-align: center;
    border-radius: 20px;
    /* Center the images when snapping */
    margin-right: 10px;
    /* Adjust spacing between images */
}

/* Optional: Add margins or padding to adjust spacing between images */
.image-wrapper>img:hover {
    scale: 1.2;
}

/* Optional: Style the images */
.image-wrapper>img {
    max-width: 100%;
    /* Ensure images don't exceed container width */
}

.icons {
    display: flex;
    align-items: center;
    gap: 4rem;
    /* margin-right: 25px; */
}

.icons a {
    color: white;
    transition: color 0.3s ease;
}

.icons a:hover {
    color: black;
}

.icons > .contributionName{
    display: flex !important;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 5px;
    font-weight: 600;
    font-size: 16px;
    font-family: Arial, Helvetica, sans-serif;
}

.contributor-details {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: white;
    padding: 1rem;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    z-index: 1;
}

.contributor-details p {
    margin: 0;
}

.project-images {
    overflow-x: auto;
}

.image-wrapper {
    display: flex;
    gap: 1rem;
}

.image-wrapper img {
    max-width: 100%;
}