.framework-carousel {
    display: flex;
    overflow-x: scroll;
    scroll-behavior: smooth;
    scrollbar-width: none;
    /* Hide scrollbar */
    -ms-overflow-style: none;
    gap: 10px;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
    padding-bottom: 100px;
    /* Hide scrollbar for IE/Edge */
}

.framework-carousel::-webkit-scrollbar {
    display: none;
    /* Hide scrollbar for Chrome/Safari/Opera */
}

.framework-carousel > img {
    width: 200px;
    /* Adjust the width as needed */
    height: 200px;
    /* Adjust the height as needed */
    object-fit: cover;
    /* Ensure the image fills the container without distortion */
    background: #39b54a;
    /* color: #39b54a; */
}

.framework-carousel > img:hover{
    scale: 1.05;
}


.fw > h1 {
    display: flex;
    color: #39b54a;
    position: relative;
    width: 100vw;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    font-family: Arial, Helvetica, sans-serif;
    text-decoration: underline;
}